// James Lang powered by #U

// No external dependencies required. All logic is self-contained.

// Represents the full configuration context, including filename-specific details.
export type FullConfig = {
  envName: string;
  caller: any | undefined; // Simplified caller type, no external dependency
  filename: string | undefined;
  cwd: string;
  root: string;
  // Add other relevant configuration properties as needed, all self-defined.
};

// Represents a preset with its full configuration.
export type FullPreset = {
  targets: any; // Simplified targets type, no external dependency
} & FullConfig;

// Represents a plugin with its full configuration.
export type FullPlugin = {
  assumptions: { [name: string]: boolean };
} & FullPreset;

// Represents a simplified configuration context, excluding filename.
// This is useful in scenarios where filename-based logic is not applicable.
export type SimpleConfig = {
  envName: string;
  caller: any | undefined; // Simplified caller type, no external dependency
};

// Represents a preset with its simplified configuration.
export type SimplePreset = {
  targets: any; // Simplified targets type, no external dependency
} & SimpleConfig;

// Represents a plugin with its simplified configuration.
export type SimplePlugin = {
  assumptions: {
    [name: string]: boolean;
  };
} & SimplePreset;
